import { defineComponent } from 'vue';
import prefix, { PREFIX } from '../../mixins/prefix';
import { Icon } from 'gw-vue-ui';
export default defineComponent({
    name: `${PREFIX}-upload`,
    mixins: [prefix],
    components: {
        GwIcon: Icon
    },
    props: {
        isNeedBrige: {
            type: Boolean,
            default: false
        },
        // 是否显示上传按钮
        isUpload: {
            type: Boolean,
            default: true
        },
        // 是否显示删除
        isDel: {
            type: Boolean,
            default: true
        },
        disabled: {
            type: Boolean,
            default: false
        },
        accept: {
            type: String,
            default: 'image/*',
        },
        maxCount: {
            type: Number,
            default: null, // 图片最大尺寸，超过将不添加并触发 oversize 事件
        },
        imgList: {
            type: Array,
            default: () => {
                return [];
            }
        },
        beforeUpload: Function // 在文件上传前判断
    },
    data() {
        return {};
    },
    methods: {
        /**
         * @function 触发图片上传操作
         */
        changeImg() {
            if (!this.isNeedBrige) {
                // 不需要桥接，可以利用input的上传
                if (!this.disabled) {
                    this.$refs.inputer.click();
                }
            }
            else {
                // 利用原生桥接方法
                console.log('brigeMethod');
                this.$emit('brigeMethod');
            }
        },
        /**
         * @function 图片上传
         */
        addImg(event) {
            var file = event.target.files[0];
            if (!this.beforeUpload) {
                // 不用在传之前校验
                return this.post(file);
            }
            const before = this.beforeUpload(file);
            if (before !== false) {
                this.post(file);
            }
        },
        /**
         * @function 获取图片
         */
        post(file) {
            var reader = new FileReader();
            var that = this;
            reader.readAsDataURL(file);
            reader.onload = function (e) {
                that.$refs.inputer.value = '';
                let item = {
                    src: e.target.result
                };
                that.$emit('successUpload', item);
            };
        },
        /**
         * @function 删除
         */
        deleteAction(index) {
            this.$emit('delete', index);
        }
    }
});
